@import 'styles/_constants';

.base {
    width: 100%;
    max-width: 400px;
}

.caption {
    text-align: center;
    color: $secondary-text-color;
}

.source {
    display: flex;
    justify-content: center;
    overflow: auto;
}

.sequence {
    white-space: nowrap;
    height: 75px;
    padding: 2px;
    overflow: auto;
    margin: 2px;
    border-radius: 5px;
    
    &__over {
        background-color: lighten($base-color, 20);
    }
}

@keyframes blink {
    0% {
        background-color: lighten($secondary-color, 20);
    }
    100% {
        background-color: darken($secondary-color, 10);
    }
}

.item {
    display: inline-block;
    height: 50px;
    width: 50px;
    user-select: none;
    margin: 0 2px 0 0;
    border-radius: 10px;
    border: 1px solid $secondary-color;
    background-color: darken($secondary-color, 10);

    &__dragging {
        opacity: 0.3;
    }

    & img {
        width: 100%;
        height: 100%;
    }

    &__blink {
        animation: blink 0.5s;
    }
}

.note {
    height: 50px;
    width: 50px;
    user-select: none;
    margin: 0 2px 0 0;
    border: 1px solid $secondary-color;
    border-radius: 10px;
    background-color: darken($secondary-color, 10);
    transition-duration: 0.001s;

    &__dragging {
        width: 0;
    }

    & img {
        width: 100%;
        height: 100%;
    }
}
